/* Mask Screen */
#mask {
  position: fixed;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 1; /* Full black screen initially */
  z-index: 999; /* Make sure it's on top */
  transition: opacity 1s ease-out; /* Fade out over 1 second */
}
